.footer {
    transform: translateY(950px);
    z-index: 4;
    display: flex;
    flex-direction: row;
    height: 20vh;
}
#leftFooter {
    text-align: left;
}
#titleLeftFooter , #titleRightFooter {
    color: rgb(183, 229, 251);
}
#rightFooter {
    width: 70%;
    text-align: right;
}
#linkLeftFooter , #linkRightFooter {
    padding-top: 30px;
}
#linkLeftFooter > a {
    padding-right: 20px;
}
#linkRightFooter > a {
    padding-left: 20px;
}
@media screen and (max-width: 500px) {
    .footer {
        transform: translateY(100px);
    }
    
}